body {
  font: 300 16px /1.5em 'Lato', sans-serif;
}

/*links*/
a {
  color: #a29061;
  text-decoration: none;
}

a:hover{
  opacity: 0.8;
}

/*images*/
img{
  max-width: 100%;
  height: auto;
}

/*grid*/
.container{
  max-width: 1920px;
  width: 100%;
  margin: 0 auto;
  overflow: hidden;
}

/*Page Header*/
.page-header{
  padding:55px 0;
}

.page-header > .container{
  display:flex;
  justify-content: space-between;
  align-items: center;
}

.brand .brand img{
  display: block;
}

/*Menu*/
.menu-items{
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  align-items: center;
}

.menu-items li.menu::before{
  position:absolute;
  content:'';
  height:4.3rem;
  border-left: 1px solid;
  margin-top:-4rem;
  margin-left:.6rem;
  color: #2d3439;
  opacity: 0.1;
}

.menu-items li.active::before{
  position:absolute;
  content:'';
  height:4.3rem;
  border-left: 1px solid;
  margin-top:-4rem;
  margin-left:.6rem;
  color: #4d565d;
  opacity: 1;
}

.menu-items li{
  margin: 0 4px;
  display: inline-block;
  list-style: none;
}

.menu-items li a{
  padding: 10px 10px;
  display: inline-block;
  font-size: 18px;
  line-height: 1.2em;
  font-weight: 400;
  letter-spacing: 0.05px;
}

.menu-items .active{
  color: #4d565d;
}

.menu-items .menu-icon a{
  background-repeat: no-repeat;
  padding: 0;
  width: 70px;
  height: 13px;
}

.menu-line{
  background-image: url('../img/menu line.svg');

}
/*Top Section*/
.top-section{
  background-image: url('../img/top-bg.png');
  background-size: cover;
  height:1150px;
  width: auto;
}

.section-header {
  color: #af9a63;
  font-size: 1.5em;
  font-weight: 400;
  line-height: 2.5em;
}

.section-header .active{
  border-bottom: 2px solid;
  color: #af9a63;
  width:417px;
}

.section-header .passive{
  opacity: 0.5;
}

.top-section-title{
  text-transform: uppercase;
  margin-top: -100px;
  margin-left: 75px;
}

.top-section-title h1{
  color: #455560;
  font-size: 5em;
  font-weight: 300;
  line-height: 1.4em;
  z-index:10;
}

.top-section-title h2 {
  color: #a29061;
  font-size: 3.75em;
  line-height: 1.4em;
  font-weight: 300;
}

.arrow{
  margin-top:350px;
  display: flex;
  justify-content: flex-end;
}

/*welcome-section*/
.welcome-section{
  background-image: url('../img/welcome bg grey.png');
  background-size: contain;
  background-repeat: no-repeat;
  height:1000px;
  width: auto;
  position:relative;
  color:#ffffff;
}

.welcome-section .container{
  display:flex;
}

.welcome-section .content{
  margin-top: 150px;
  width: 45%;
}

.content h2{
  font-size: 3.75em;
  text-transform:uppercase;
  font-weight: 300;
}

.content h4{
  font-size: 2.5em;
  text-transform:uppercase;
  font-weight: 300;
  color: #a29061;
}

.content p {
  font-weight: 400;
  line-height: 26px;
  letter-spacing: 0.16px;
}

button{
  width: 219px;
  height: 219px;
  background-color: transparent;
  border: none;
  position:relative;
  margin-top:690px;
  margin-right:100px;
}

/*news-section*/
.news-section {
  background-image: url('../img/news bg.png');
  background-size: cover;
  height: 800px;
  width: auto;
  margin-top: -300px;
}

/*page footer*/

.page-footer .links{
  background-color:#455560;
  color: #54626c;
}

.site-footer__bottom {
  background-color: #2d3439;
  color: #575d61;
}


.site-footer__bottom .container{
  display:flex;
  justify-content: space-between;
  align-items: center;
}

.site-footer__bottom .contact{
  display:flex;
  justify-content: space-between;
  align-items: center;
}
